<!-- LottoGenerator.vue -->
<template>
  <div class="lotto-generator">
    <h2>로또 번호 생성기</h2>
    <button @click="generateLottoNumbers" :disabled="lottoRecords.length >= 100">번호 생성</button>
    <button @click="resetLottoNumbers">초기화</button>
    <p v-if="lottoRecords.length >= 100">초기화후 다시 생성하세요</p>
    <div v-if="lottoRecords.length > 0" class="numbers">
      <div v-for="(record, index) in lottoRecords" :key="index" class="record">
        <p>{{ record.date }} - {{ record.numbers.join(', ') }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      lottoRecords: []
    }
  },
  methods: {
    generateLottoNumbers () {
      if (this.lottoRecords.length < 100) {
        const numbers = []
        while (numbers.length < 6) {
          const randomNumber = Math.floor(Math.random() * 45) + 1
          if (!numbers.includes(randomNumber)) {
            numbers.push(randomNumber)
          }
        }
        const date = new Date().toLocaleString()
        this.lottoRecords.unshift({ date, numbers: numbers.sort((a, b) => a - b) })
      }
    },
    resetLottoNumbers () {
      this.lottoRecords = []
    }
  }
}
</script>

<style scoped>
.lotto-generator {
  text-align: center;
  margin-top: 20px;
}

.numbers {
  margin-top: 10px;
}

.record {
  margin-bottom: 10px;
}

.record p {
  display: inline-block;
  margin: 5px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
</style>
