import { createRouter, createWebHashHistory } from 'vue-router'
// import home from '../views/HomeView.vue'
import qrcode from '../views/QrcodeGenerator.vue'
import lotto from '../views/LottoGenerator.vue'
import lottomy from '../views/LottoMyGenerator.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: qrcode,
    meta: { title: 'Home' }
  },
  {
    path: '/qrcode',
    name: 'qrcode',
    component: qrcode,
    meta: { title: 'QRCode' }
  },
  {
    path: '/lotto',
    name: 'lotto',
    component: lotto,
    meta: { title: 'Lotto' }
  },
  {
    path: '/lottomy',
    name: 'lottomy',
    component: lottomy,
    meta: { title: 'lottomy' }
  },
  {
    path: '/:pathMatch(.*)*',
    redirect: '/'
  }
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

export default router
